@import 'styles/mixins.scss';

.card {
  width: 100%;
  padding: 4rem 0;
  margin-right: 4rem;


  @media only screen and (max-width: $tablet) {
    padding: 2rem 0;
  }

  button {
    @include font(normal, 800, 16rem, 16rem);
    white-space: nowrap;
    text-transform: uppercase;
    text-shadow: -0.125rem -0.125rem 0 $white, 0.125rem -0.125rem 0 $white, -0.125rem 0.125rem 0 $white, 0.125rem 0.125rem 0 $white;
    color: $darkGray;
    -webkit-user-select: none;

    @media only screen and (max-width: $tablet) {
      @include font(normal, 800, 10rem, 10rem);
    }
  }

  img {
    position: absolute;
    height: 100%;
    opacity: 0;
    z-index: -1;
  }

  &:hover {
    img {
      opacity: 1;
    }

    button {
      color: $white;
    }
  }
}